import { Injectable } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material';
import { Observable } from 'rxjs/Observable';
import { TaskActionDto } from '../../../proxies';
import { UploadDocumentDialogResult } from './upload-document-dialog-result';
import { UploadDocumentComponent } from './upload-document.component';

@Injectable()
export class UploadDocumentService {

  constructor(private dialog: MatDialog) {
  }

  upload(action: TaskActionDto, placeholder: string): Observable<UploadDocumentDialogResult> {
    const dialogRef: MatDialogRef<UploadDocumentComponent> =
      this.dialog.open(UploadDocumentComponent, {
        disableClose: true
      });

    dialogRef.componentInstance.acceptedFileTypes = this.getAcceptedFileTypes(action);

    dialogRef.componentInstance.message = action.name;
    dialogRef.componentInstance.placeholder = placeholder;
    dialogRef.componentInstance.required = (action.actionComment === 2);
    dialogRef.componentInstance.canComment = (action.actionComment > 0);

    return dialogRef.afterClosed()
      .map((result: UploadDocumentDialogResult) => {
        return result;
      });
  }

  private getAcceptedFileTypes(action: TaskActionDto): string {
    if (!action.document) {
      return '';
    }

    switch (action.document.documentType) {
      case 'xlsx':
        return 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel';
      case 'pdf':
        return '.pdf';
      case 'docx':
        return '.doc, .docx';
      default: {
        return '';
      }
    }
  }
}



