import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CanDeactivateGuard } from '../core/can-deactivate-guard/can-deactivate-guard.service';
import { MyTaskListComponent } from './my-task-list/my-task-list.component';
import { TaskDetailResolver } from './task-detail/task-detail-resolver.service';
import { TaskDetailComponent } from './task-detail/task-detail.component';

const routes: Routes = [
  {path: '', component: MyTaskListComponent, data: {title: 'My task list'}},
  {
    path: ':jobId/:taskId', component: TaskDetailComponent, canDeactivate: [ CanDeactivateGuard ],
    resolve: {
      task: TaskDetailResolver
    }
  }
];

@NgModule({
  imports: [ RouterModule.forChild(routes) ],
  exports: [ RouterModule ],
})
export class TaskRoutingModule {
}

export const routedComponents: Array<any> = [
  MyTaskListComponent,
  TaskDetailComponent
];



