import { Component, ElementRef, OnDestroy, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { environment } from '../../../environments/environment';
import { CoreService } from '../../core/core.service';
import { ExecuteActionDialogResult } from '../../core/dialog/execute-action/execute-action-dialog-result';
import { NotificationMessage } from '../../core/notification/notification-message';
import { ActionTypeListService } from '../../job-definition/detail/action/action-type-list.service';
import {
  DocumentService,
  FriggResponse,
  GetDocumentDto,
  JobTaskActionParameterDto,
  TaskActionDto,
  TaskDto,
  TaskService
} from '../../proxies';
import { TaskDetailResolver } from './task-detail-resolver.service';
import { UploadDocumentDialogResult } from './upload-document/upload-document-dialog-result';
import { UploadDocumentService } from './upload-document/upload-document.service';


@Component({
  selector: 'frigg-task-detail',
  templateUrl: 'task-detail.component.html',
  styleUrls: [ 'task-detail.component.scss' ],
  encapsulation: ViewEncapsulation.None
})
export class TaskDetailComponent implements OnInit, OnDestroy {
  task: TaskDto;
  mq: MediaQueryList = window.matchMedia('(max-width: 48em)');

  @ViewChild('download') downloadLink: ElementRef;

  constructor(private route: ActivatedRoute,
              private router: Router,
              private taskService: TaskService,
              private documentService: DocumentService,
              private resolver: TaskDetailResolver,
              private core: CoreService,
              private uploadDocument: UploadDocumentService,
              public actionTypeListService: ActionTypeListService) {
  }

  get isMobile(): boolean {
    return this.mq.matches;
  }

  get isJobActive(): boolean {
    return this.task.job.status === 'Active';
  }

  get actions(): TaskActionDto[] {
    const nonWorkflowActions: TaskActionDto[] = this.task.actions
      .filter((action: TaskActionDto) =>
        this.actionTypeListService.isNonFinishing(action.actionType))
      .map((action: TaskActionDto) => {
        if (!this.isActionEnabled(action)) {
          (action as any).className = 'disabled-action';
        }
        return action;
      });

    return this.isMobile ?
      nonWorkflowActions.concat(this.workflowActions) :
      nonWorkflowActions;
  }

  get workflowActions(): TaskActionDto[] {
    return this.task.actions
      .filter((action: TaskActionDto) =>
        this.actionTypeListService.isFinishing(action.actionType))
      .map((action: TaskActionDto) => {
        (action as any).className = this.isActionEnabled(action) ?
          'active-workflow-action' :
          'disabled-workflow-action';

        return action;
      });
  }

  isActionDone(action: TaskActionDto): boolean {
    return action.actionCount > 0;
  }


  isActionEnabled(action: TaskActionDto): boolean {
    return this.isJobActive && action.enabled;
  }

  isWorkflowAction(action: TaskActionDto): boolean {
    if (!action) {
      return false;
    }
    return this.actionTypeListService.isFinishing(action.actionType);
  }

  ngOnInit(): void {
    this.core.setTranslatedNavigateText('frigg.myTaskList');
    this.core.shell.setNavigateUrl('/tasks');

    this.route.data
      .subscribe((data: { task: TaskDto }) => {
          this.task = data.task;
          this.core.shell.setHeader(this.task.job.name);
          this.startListeningForNotifications();
        }
      );
  }

  ngOnDestroy(): void {
    this.stopListeningForNotifications();
  }

  filterActions(): void {

  }

  startListeningForNotifications(delay: number = 0): void {
    setTimeout(() => {
      this.core.notification
        .listen('private-task-changed' + this.task.id, 'TaskChanged')
        .subscribe((message: NotificationMessage<any>) => {
          this.core.toast.info('Task has changed');
          this.resolver.resolve(this.route.snapshot, this.router.routerState.snapshot)
            .subscribe((task: TaskDto): void => {
              this.task = task;
              this.core.shell.setHeader(this.task.job.name);
              if (task.job.status === 'Completed') {
                this.goToTaskList();
              }
            })
        });
    }, delay);
  }

  stopListeningForNotifications(): void {
    this.core.notification.stopListening('private-task-changed' + this.task.id);
  }


  onClickAction(action: TaskActionDto): void {
    if (!this.isJobActive || !action.enabled) {
      return;
    }

    const placeholder = action.actionComment === 2 ?
      this.core.translate('frigg.commentRequired') :
      this.core.translate('frigg.commentOptional');

    const actionDoneText = this.actionTypeListService
      .isFinishing(action.actionType) ?
      this.core.translate('frigg.ok') :
      this.core.translate('frigg.done');

    const header = this.actionTypeListService
      .isFinishing(action.actionType) ?
      action.name + '?' :
      action.name;

    switch (action.actionType) {
      case 'attachDocument':
      case 'checkinDocument': {
        this.uploadDocument.upload(action, placeholder)
          .subscribe((result: UploadDocumentDialogResult) => {
            if (result.ok) {
              this.executeUploadAction(action, result.comment, result.fileName, result.fileDate, result.fileContent);
            }
          });
        break;
      }
      case 'checkoutDocument': {
        this.executeDownloadAction(action);
        break;
      }
      case 'gotoUrl': {
        this.executeGotoUrlAction(action);
        break;
      }
      default: {
        this.core.dialog.executeAction(
          header,
          placeholder,
          action.actionComment,
          actionDoneText
        ).subscribe((result: ExecuteActionDialogResult) => {
          if (result.ok) {
            this.executeAction(action, result.comment);
          }
        });
      }

    }

  }

  executeUploadAction(action: TaskActionDto, comment: string, fileName: string, fileDate: Date, fileContent: string): void {
    this.stopListeningForNotifications();

    this.taskService.executeUploadAction({
      jobId: this.task.job.id,
      taskId: this.task.id,
      actionId: action.id,
      comment,
      fileName,
      fileDate,
      fileContent
    }).subscribe((response: FriggResponse<TaskDto>) => {
      if (response.success) {
        this.task = response.result;
        this.core.toast.success(this.getActionSuccessMessage(action));
      }

      this.startListeningForNotifications(1000);
    });
  }

  executeGotoUrlAction(action: TaskActionDto): void {
    this.openUrl(action);

    this.stopListeningForNotifications();
    this.taskService.executeAction({
      jobId: this.task.job.id,
      taskId: this.task.id,
      actionId: action.id,
      comment: ''
    })
      .subscribe((response: FriggResponse<TaskDto>) => {
        if (response.success) {
          this.task = response.result;

          this.startListeningForNotifications(1000);
        }
      });
  }

  executeDownloadAction(action: TaskActionDto): void {
    this.stopListeningForNotifications();
    this.taskService.executeAction({
      jobId: this.task.job.id,
      taskId: this.task.id,
      actionId: action.id,
      comment: ''
    })
      .subscribe((response: FriggResponse<TaskDto>) => {
        if (response.success) {
          this.task = response.result;

          this.documentService.getLatestDocumentRevision(this.task.job.id, action.document.id)
            .subscribe((documentResponse: FriggResponse<GetDocumentDto>) => {
              if (documentResponse.success) {
                this.downloadFileContentResult(this.task.job.id, action.document.id);
                // this.downloadResponse(this.task.job.id, action.document.id);
              }
            });
          this.startListeningForNotifications(1000);
        }
      });
  }

  downloadFileContentResult(jobId: number, documentId: number): void {
    const url = `${environment.apiPath}/api/services/cplan/Document/${jobId}/${documentId}`;
    window.location.href = url;
    // const downloadElement = this.downloadLink.nativeElement as HTMLAnchorElement;
    // downloadElement.href =  url;
    // downloadElement.click();
  }

  downloadResponse(jobId: number, documentId: number): void {
    this.documentService.getLatestDocumentRevision(jobId, documentId)
      .subscribe((response: FriggResponse<GetDocumentDto>) => {
        if (response.success) {
          const file = new File([ btoa(response.result.fileContent) ], response.result.fileName);
          const objectURL = window.URL.createObjectURL(file);

          const downloadElement = this.downloadLink.nativeElement as HTMLAnchorElement;
          downloadElement.href = objectURL;
          downloadElement.click();
        }
      });
  }

  executeAction(action: TaskActionDto, comment: string): void {
    this.stopListeningForNotifications();
    this.taskService.executeAction({
      jobId: this.task.job.id,
      taskId: this.task.id,
      actionId: action.id,
      comment
    })
      .subscribe((response: FriggResponse<TaskDto>) => {
        if (response.success) {
          this.task = response.result;
          this.core.toast.success(this.getActionSuccessMessage(action));

          if (this.actionTypeListService.isFinishing(action.actionType) && this.isTaskCompleted(action)) {
            this.goToTaskList();
          } else {
            this.startListeningForNotifications(1000);
          }
        }
      });
  }

  private openUrl(action: TaskActionDto): void {
    const urlParameter = action.parameters
      .find((p: JobTaskActionParameterDto) => p.description === 'url');

    if (urlParameter && urlParameter.value) {
      let url = urlParameter.value;

      if (url.indexOf('://') < 0) {
        url = `http://${url}`;
      }

      window.open(url, '_blank');
    }
  }

  private goToTaskList(): void {
    this.router.navigate([ 'tasks' ]);
  }

  private isTaskCompleted(action: TaskActionDto): boolean {
    return this.actionTypeListService.isFinishing(action.actionType)
      && this.task.actions.every((a: TaskActionDto) => !a.enabled);
  }

  private getActionSuccessMessage(action: TaskActionDto): string {
    if (this.actionTypeListService.isFinishing(action.actionType)) {
      return action.actionType === 'return'
        ? this.core.translate('frigg.taskReturnedMessage')
        : this.core.translate('frigg.taskCompletedMessage');
    } else {
      return this.core.translate('frigg.executedAction', {name: action.name})
    }
  }
}



