import { NgModule } from '@angular/core';
import { ActionTypeListService } from '../job-definition/detail/action/action-type-list.service';
import { LayoutModule } from '../shared/layout/layout.module';
import { SharedModule } from '../shared/shared.module';
import { TileListModule } from '../shared/tile-list/tile-list.module';
import { MyTaskListTileComponent } from './my-task-list/my-task-list-tile.component';
import { TaskDetailResolver } from './task-detail/task-detail-resolver.service';
import { UploadDocumentComponent } from './task-detail/upload-document/upload-document.component';
import { UploadDocumentService } from './task-detail/upload-document/upload-document.service';
import { routedComponents, TaskRoutingModule } from './task.routing';

@NgModule({
  imports: [
    TaskRoutingModule,
    SharedModule,
    LayoutModule,
    TileListModule
  ],
  declarations: [
    ...routedComponents,
    MyTaskListTileComponent,
    UploadDocumentComponent
  ],
  exports: [],
  providers: [
    TaskDetailResolver,
    ActionTypeListService,
    UploadDocumentService
  ],
  entryComponents: [
    UploadDocumentComponent
  ]
})
export class TaskModule {

}



