import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import 'rxjs/add/operator/filter';
import { CoreService } from '../../core/core.service';
import { FriggResponse, GetCurrentLoginInformationsOutput, ListResultDto, SessionService, TaskListDto, TaskService } from '../../proxies';

@Component({
  selector: 'frigg-my-task-list',
  templateUrl: 'my-task-list.component.html',
  styleUrls: [ 'my-task-list.component.scss' ]
})
export class MyTaskListComponent implements OnInit, OnDestroy {
  tasks: TaskListDto[] = [];
  userId = 0;

  constructor(private taskService: TaskService,
              private core: CoreService,
              private session: SessionService,
              private router: Router) {
  }

  ngOnInit(): void {
    this.core.setTranslatedShellHeader('frigg.myTaskList');
    this.core.setTranslatedNavigateText('frigg.homePage');
    this.core.shell.setNavigateUrl('/dashboard');

    this.getMyTaskList();

    this.session.getCurrentLoginInformations()
      .filter((response: FriggResponse<GetCurrentLoginInformationsOutput>) => response.success)
      .subscribe((response: FriggResponse<GetCurrentLoginInformationsOutput>) => {
        this.onGetUserId(response.result.user.id);
      });
  }

  ngOnDestroy(): void {
    this.core.notification.stopListening('private-TaskListChange' + this.userId);
  }

  onItemClick(task: TaskListDto): void {
    this.router.navigate([ `./tasks/${ task.job.id }/${ task.id }` ]);
  }

  private onGetUserId(userId: number): void {
    if (this.userId > 0) {
      this.core.notification.stopListening('private-TaskListChange' + this.userId);
    }

    this.userId = userId;
    this.core.notification.listen('private-TaskListChange' + userId, 'TaskListChange')
      .subscribe(() => {
        this.getMyTaskList();
      });
  }

  private getMyTaskList(): void {
    this.taskService.getMyTaskList()
      .subscribe((response: FriggResponse<ListResultDto<TaskListDto>>) => {
        if (response.success) {
          this.tasks = response.result.items;
        }
      });
  }
}



