import { AfterViewInit, Component, ElementRef, Input, OnDestroy, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { MatDialogRef } from '@angular/material';
import { DOM } from 'rx-dom';
import { FromObservable } from 'rxjs/observable/FromObservable';
import { ThemeService } from '../../../core/theme/theme.service';

@Component({
  selector: 'frigg-upload-document',
  templateUrl: './upload-document.component.html',
  styleUrls: [ './upload-document.component.scss' ],
  encapsulation: ViewEncapsulation.None
})
export class UploadDocumentComponent implements OnInit, AfterViewInit, OnDestroy {

  @Input() message: string;
  @Input() doneText = 'Ok';
  @Input() acceptedFileTypes = '';

  fileResult: { file: File, binaryString: string };

  @ViewChild('fileInput') fileInput: ElementRef;

  comment = '';
  placeholder = '';
  canComment = true;
  required = false;

  constructor(public themeService: ThemeService,
              public dialogRef: MatDialogRef<UploadDocumentComponent>) {
  }

  get fileName(): string {
    return this.fileResult ?
      this.fileResult.file.name :
      ''
  }

  get canExecute(): boolean {
    return this.fileResult &&
      (!this.required || (this.required && this.comment.trim().length > 0));
  }

  ngOnInit(): void {
  }

  ngOnDestroy(): void {
  }

  ngAfterViewInit(): void {
    DOM.change(this.fileInput.nativeElement)
      .flatMap((event: Event): any => {
        const inputEl: HTMLInputElement = event.target as HTMLInputElement;
        return FromObservable.create(inputEl.files || []);
      })
      .flatMap((file: File) => {
        const xDOM: any = DOM as any;
        return xDOM.fromReader(file).asBinaryString().map((
          (binaryString: string): { file: File, binaryString: string } => {
            return {file, binaryString};
          }
        ));
      })
      .subscribe((result: { file: File, binaryString: string }) => {
        this.fileResult = result;
      });

    this.fileInput.nativeElement.click();
  }

  onClickOk(): void {
    this.dialogRef.close({
      ok: true,
      comment: this.comment,
      fileName: this.fileResult.file.name,
      fileDate: this.fileResult.file.lastModifiedDate,
      fileContent: btoa(this.fileResult.binaryString)
    });
  }

}



